<template>
  <div>
    <v-sheet class="rounded-lg pa-2" outlined height="80">
      <v-row class="pa-3">
        <div class="mx-3">
          <span class="mt-2 title">Mode Malam</span>
          <div v-if="!$vuetify.theme.dark" bottom>
            <span class="mr-1">Tidak Aktif</span>
            <v-icon color="yellow">mdi-white-balance-sunny</v-icon>
          </div>
          <div v-else bottom>
            <span>Aktif</span>
            <v-icon color="orange">mdi-moon-waxing-crescent</v-icon>
          </div>
        </div>
        
        <v-spacer></v-spacer>
        <div>
          <v-switch
            v-model="switchModeMalam"
            @change="toggledarkmode()"
            hide-details
            inset
            color="error"
          ></v-switch>
          
          <!-- <div class="mt-3 mx-3">
            <v-tooltip v-if="!$vuetify.theme.dark" bottom>
              <template v-slot:activator="{ on }">
                <v-btn v-on="on" color="info" depressed small fab @click="toggledarkmode">
                  <v-icon color="yellow">mdi-white-balance-sunny</v-icon>
                </v-btn>
              </template>
              <span>Off</span>
            </v-tooltip>

            <v-tooltip v-else bottom>
              <template v-slot:activator="{ on }">
                <v-btn v-on="on" color="dark" outlined small fab @click="toggledarkmode">           
                  <v-icon color="orange">mdi-moon-waxing-crescent</v-icon>
                </v-btn>
              </template>
              <span>On</span>
            </v-tooltip>
          </div> -->
        </div>
      </v-row>
    </v-sheet>
  </div>
</template>
<script>
import Vue from 'vue'
import Vuetify from 'vuetify/lib'

Vue.use(Vuetify)
export default {
    data: () => ({switchModeMalam: ""}),
    methods: {
        toggledarkmode: function () {
            this.$vuetify.theme.dark = !this.$vuetify.theme.dark;
            localStorage.setItem("darktheme", this.$vuetify.theme.dark.toString());
            console.log(this.$vuetify.theme.dark);
        },
    },
    mounted() {
        const theme = localStorage.getItem("darktheme");
        if (theme) {
            if (theme == "true") {
                this.$vuetify.theme.dark = true;
                this.switchModeMalam = this.$vuetify.theme.dark;
            } else {
                this.$vuetify.theme.dark = false;
                this.switchModeMalam = this.$vuetify.theme.dark;
            }
        }
    },
};
</script>